import Vue from 'vue';
import App from './App.vue';
import { loadState } from '@nextcloud/initial-state';
Vue.prototype.t = t;
Vue.prototype.OC = OC;
var clients = loadState('oauth2', 'clients');
var View = Vue.extend(App);
var oauth = new View({
  propsData: {
    clients: clients
  }
});
oauth.$mount('#oauth2');